.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#logo1 {
  align-items: center;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.map-h2 {
  text-transform: uppercase;
  font-size: 1rem;
  padding: 20px;
  padding-left: 10px;
  text-align: center;
}

.google-map {
  width: 80%;
  height: 60vh;
}

.pin {
  display: flex;
  align-items: center;
  width: 180px;
  color: var(--main-blue);
}

.pin-icon {
  font-size: 4rem;
}

.pin-text {
  font-size: 1.3em;
}

@media screen and (min-width: 799px) {
  .google-map {
    height: 40vh;
  }

  .map-h2 {
    font-size: 1.3rem;
    font-weight: 400;
  }

  .pin {
    width: 15vw;
  }

  .pin-icon {
    font-size: 10vw;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #25333d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn {
  color: white;
  opacity: 0.8;
  background-color: #39bd76;
}

.btn:hover {
  color: white;
  opacity: 1;
}

.btn:focus {
  border-color: rgb(137, 199, 166);
  box-shadow: 0 0 0 0.1rem rgba(137, 199, 166, 0.5);
}

/* Button used to open the contact form - fixed at the bottom of the page */
.open-button {
  background-color: #1b2730;
  padding: 10px 10px;
  border: none;
  cursor: pointer;
  opacity: 0.8;
  bottom: 23px;
  right: 28px;
  width: 100px;
  opacity: 0.8;
  color: white;
}

/* The popup form - hidden by default */
.form-popup {
  padding: 10px 15px 10px 15px;
  background-color: #1a242b;
  display: none;
  border-radius: 10px;
  position: fixed;
  width: 315px;
  bottom: 0;
  right: 15px;
  /* border: 1px solid #ffffff; */
  z-index: 9;
}

.chattingWith {
  font-weight: normal;
  font-size: 18px;
  color: white;
}

#messageBox {
  border-radius: 5px;
  background-color: rgb(250, 250, 250);
  font-size: small;
}

/* Full-width input fields */
.form-container input[type="text"] {
  width: 100%;
  padding: 5px;
  margin: 10px 0 10px 0;
  border: none;
  background: #ffffff;
}

/* When the inputs get focus, do something */
.form-container input[type="text"]:focus {
  background-color: rgb(255, 255, 255);
  outline: none;
}

.form-control {
  background-color: #fafafa;
  color: rgb(100, 100, 100);
}

.form-control:focus {
  background-color: #ffffff;
  color: rgb(75, 75, 75);
  border-color: #39bd76;
  box-shadow: 0 0 0 0.1rem rgba(57, 189, 118, 0.1);
}

