.btn-primary {
  color: #fff;
  background-color: #1b2730;
  border-color: #1b2730;
}

.btn-primary:hover {
  color: #fff;
  background-color: #222f3a;
  border-color: #222f3a;
}

.btn-primary:focus,
.btn-primary.focus {
  color: #fff;
  background-color: #222f3a;
  border-color: #222f3a;
  box-shadow: 0 0 0 0.2rem rgba(13, 18, 23, 0.5);
}

.btn-primary.disabled,
.btn-primary:disabled {
  color: #fff;
  background-color: #222f3a;
  border-color: #222f3a;
}

.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.show > .btn-primary.dropdown-toggle {
  color: #fff;
  background-color: #1b2730;
  border-color: #1b2730;
}

.btn-primary:not(:disabled):not(.disabled):active:focus,
.btn-primary:not(:disabled):not(.disabled).active:focus,
.show > .btn-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(13, 18, 23, 0.5);
}

